import { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
// import classnames from "classnames";

import {PROTOCOL_RELATIONSHIPS as PR } from "@meddbriefer/scenario-data/constants"
import { groupByKey } from "@meddbriefer/scenario-data/utils"


import ScenarioEditorLayout from "./ScenarioEditorLayout"
import {useScenarioEditor} from "./ScenarioEditorContext"

const InterventionAnswers = () => {
    const {scenarioId} = useParams()
    const {scenario, scenarioType} = useScenarioEditor()
    const [state, setState] = useState()

    const saveHandler = (event) => {
        console.log("save handler clicked")
    }
    useEffect(
        () => {
            if (!state) {

                // get set of all intervention IDs listed in actions
                const intvIDs = Array.from(new Set(scenario.actions.filter(act => act.protocolRelationship !== PR.CONTRA).map(e => e.interventionID)))
                // filter this down to only interventions which have prompts
                const intvIDsWithPrompts = []
                intvIDs.forEach(intvID => {
                    const intv = scenarioType.getIntervention(intvID)
                    if (undefined === intv.prompts) {
                        console.log(`intervention ${intvID} not found`)
                    } else {
                        if (intv.prompts.length > 0) {
                            intvIDsWithPrompts.push(intv.id)
                        }
                    }
                })
                console.log(intvIDsWithPrompts)
                // get all actions which refer to these interventions
                const relevantActions = scenario.actions.filter(act => intvIDsWithPrompts.includes(act.interventionID))
                console.log(relevantActions)

                const actionsByIntvID = groupByKey(relevantActions, "interventionID")

                // // setState(actionsByIntvID)
                console.log(actionsByIntvID)
                const intvAndVariant2Actions = []
                Object.entries(actionsByIntvID).forEach(([intvID, actions]) => {
                    console.log(intvID, actions)
                })
                setState({})

            }
        }, [scenarioId, scenario.actions, scenarioType, state]
    )

    if (!state) {
        return (<div>loading...</div>)
    }
    return (
        <ScenarioEditorLayout label="Intervention Answers" haveChanges={false} saveHandler={saveHandler}>
            <div id="intv-answers" className="info-wrapper">
                placeholder
            </div>
        </ScenarioEditorLayout>
    )
}

export default InterventionAnswers